import axios from 'axios';
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { LongButton } from '../atoms/Buttons';

export default function UserAbout(props) {
  const [onModifyMode, setOnModifyMode] = useState(false);
  const [about, setAbout] = useState("");

  useEffect(() => {
    axios.get(props.location.pathname)
      .then(response => setAbout(response.data.about))
  }, [props.location.pathname])

  const handleAboutChange = (e) => {
    setAbout(e.currentTarget.value);
  }

  const submitEditAbout = (e) => {
    if (e.currentTarget.value === "") {
      alert("ì†Œê°œê¸€ì´ ìž…ë ¥ë˜ì§€ ì•Šì•˜ìŠµë‹ˆë‹¤. \nì†Œê°œê¸€ì„ ìž…ë ¥í•´ ì£¼ì„¸ìš”.")
    } else {
      const editData = {
        about: about
      }
      axios.put(props.location.pathname, editData).then(() => {
        alert("ìˆ˜ì •ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤.");
        setOnModifyMode(!onModifyMode);
      })
    }

  }

  return (
    <>
      {onModifyMode ?
        <div style={{
          "display": "flex",
          "flexDirection": "column",
          "margin": "2rem auto",
          "width": "80%",
        }}>
          <textarea style={{
            "resize": "none",
            "height": "10rem",
          }}
            value={about}
            onChange={handleAboutChange}
          ></textarea>
          <div style={{
            "display": "flex"
          }}>
            <AboutButton onClick={submitEditAbout}>íŽ¸ì§‘ì™„ë£Œ</AboutButton>
            <AboutButton
              onClick={() => { setOnModifyMode(!onModifyMode) }}
            >íŽ¸ì§‘ì·¨ì†Œ</AboutButton>
          </div>
        </div>
        :
        <div style={{
          "display": "flex",
          "flexDirection": "column",
          "justifyContent": "center",
          "alignItems": "center",
          "marginTop": "1rem",
        }}>
          <AboutCard>{about === null ? "ì•„ì§ ìž‘ì„±ëœ ì†Œê°œ ê¸€ì´ ì—†ìŠµë‹ˆë‹¤." : about}</AboutCard>
          <LongButton
            style={{
              "width": "80%",
              "borderRadius": "0"
            }}
            onClick={() => {
              setOnModifyMode(!onModifyMode);
            }}

          >íŽ¸ì§‘</LongButton>
        </div>
      }
    </>
  )
}

const AboutButton = styled(LongButton)`
  width : 50%;
  border-radius : 0;
`;


const AboutCard = styled.div`
  width : 80%;
  height : 6rem;
  word-break : break-all;
  margin : 0 auto;
  padding : 0.5rem;
  border : 1px solid black;
`;
